    <div class="subscribe-mantman">
        <p class="">
            {{$t('subscribe_msg')}}
        </p>
        <form class="subscribe-form" @submit.prevent="onSubmitSubscribe">
            <input type="email" class="email-input" name="email"
                   placeholder="mantman@example.com" autocomplete="off" v-model="email" :disabled="sending">
            <button type="submit" class="subscribe-button" :disabled="sending || !isValidatedEmail">
                {{$t('subscribe')}}
            </button>
        </form>
