import Vue from 'vue'
import VueRouter from 'vue-router'

import ComingCyberpunk2077Screen from "../screens/cyberpunk2077_coming/ComingCyberpunk2077Screen";
import ComingAcValhallaScreen from "../screens/ac_valhalla_coming/ComingAcValhallaScreen";
import MainScreen from "../screens/MainScreen";
import PrivacyScreen from "../screens/PrivacyScreen";
import AppDetailScreen from "../screens/AppDetailScreen";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: MainScreen
    },
    {
        path: '/apps/:app_name',
        name: 'AppDetailScreen',
        component: AppDetailScreen
    },
    {
        path: '/coming_ac_valhalla',
        name: 'ComingAcValhalla',
        component: ComingAcValhallaScreen
    },
    {
        path: '/coming_cyberpunk_2077',
        name: 'ComingCyberpunk2077',
        component: ComingCyberpunk2077Screen
    },
    {
        path: '/privacy',
        name: 'Privacy',
        component: PrivacyScreen
    },
]

const router = new VueRouter({
    mode: 'history',
    base: '/',
    routes
})

export default router
